<template>
  <div class='channel-select'>
       <!-- <el-select v-model="articleForm.channel_id" placeholder="请选择"> -->
       <el-select :value='value' @input="$emit('input',$event)" placeholder="请选择">
            <el-option
              :label="channel.name"
              :value="channel.id"
              v-for='channel in channels'
              :key=channel.id
              ></el-option>
          </el-select>
  </div>
</template>

<script>
export default {
  data () {
    return {
      channels: []
    }
  },
  props: {
    value: {
      // type:[string]
    }
  },
  created () {
    this.loadChannels()
  },
  methods: {
    loadChannels () {
      this.$axios({
        method: 'GET',
        url: '/channels'
      }).then(res => {
        console.log(res)
        this.channels = res.data.data.channels
      })
    }

  }
}
</script>
