<template>
  <div class="commtn">
      <el-card class="box-card">
        <div slot="header" class="clearfix">
            <span>评论列表</span>
            <el-button
              style="float: right; padding: 3px 0"
              type="text"
            >操作按钮</el-button>
        </div>
          <el-table
      :data="article"
      style="width: 100%">
      <el-table-column
        prop="title"
        label="标题"
        width="180">
      </el-table-column>
      <el-table-column
        label="评论状态"
        width="180">
        <template slot-scope="scope">
          <el-switch
            v-model="scope.row.comment_status"
            active-color="#13ce66"
            inactive-color="#ff4949"
            @change='onStatusChange(scope.row)'
          >
          </el-switch>
        </template>
      </el-table-column>
      <el-table-column
        prop="total_comment_count"
        label="总评论数">
      </el-table-column>
      <el-table-column
        prop="fans_comment_count"
        label="粉丝评论数">
      </el-table-column>
      <el-table-column
        label="操作">
        <template slot-scope="scope">
           <el-button
              type="primary"
              @click='$router.push(`/comment/${scope.row.id}`)'
            >修改</el-button>
        </template>
      </el-table-column>
    </el-table>
    </el-card>
    <el-pagination
      background
      layout="prev, pager, next"
      :total="total_count"
      @current-change='onCommentChange'
      >
    </el-pagination>
  </div>
</template>

<script>
export default {
  name: 'commentArticle',
  data () {
    return {
      article: [],
      total_count: 0
    }
  },
  created () {
    this.loadArticles()
  },
  methods: {
    loadArticles (page) {
      this.$axios({
        method: 'GET',
        url: '/articles',
        params: {
          response_type: 'comment',
          page
        }
      }).then(res => {
        console.log(res.data)
        this.article = res.data.data.results
        this.total_count = res.data.data.total_count
      })
    },
    onCommentChange (page) {
      this.loadArticles(page)
    },
    onStatusChange (article) {
      this.$axios({
        method: 'PUT',
        url: '/comments/status',
        params: {
          article_id: article.id.toString()
        },
        data: {
          allow_comment: article.comment_status
        }
      }).then(res => {
        // this.$message.error`${article.comment_status ? '启用' : '关闭'}成功`
        this.$message({
          showClose: true,
          message: `${article.comment_status ? '启用' : '关闭'}成功`,
          duration: 1000,
          type: 'success'
        }).catch(err => {
          console.log(err)
          this.$message.error('操作失败')
        })
      })
    }
  }
}
</script>
