<template>
  <div class="publish">
    <el-card class="box-card">
      <div slot="header" class="clearfix" >
        <span>{{ $route.params.articleID ? '编辑文章' : '发布文章'}}</span>
      </div>
      <el-form ref="form" :model="articleForm" label-width="80px">
        <el-form-item label="标题">
          <el-input
            v-model="articleForm.title"
            placeholder="文章标题"
          ></el-input>
        </el-form-item>
        <el-form-item label="内容">
           <quill-editor v-model="articleForm.content"
             ref="myQuillEditor"
          >
        </quill-editor>
        </el-form-item>
         <el-form-item label="封面">
          <el-radio-group v-model="articleForm.cover.type">
            <!-- label前面要加:不然不是数字 -->
            <el-radio :label="1">单图</el-radio>
            <el-radio :label="3">三图</el-radio>
            <el-radio :label="0">无图</el-radio>
            <el-radio :label="-1">自动</el-radio>
          </el-radio-group>
          <!-- 遍历 选择不同的数量的图片 显示不同数量的文本框 单图是1 3图有3个文本框
          遍历数字 也会相应的产生几个文本框  -->
          <template>
            <el-row :gutter="20">
              <el-col :span="6"
                 v-for="(item,index) in articleForm.cover.type"
                  :key="item"
              >
              <div class="grid-content bg-purple">
                   <upload-image v-model="articleForm.cover.images[index]"></upload-image>
                </div>
              </el-col>
            </el-row>
          </template>

        </el-form-item>
          <el-form-item label="频道">
          <!-- <el-select v-model="articleForm.channel_id" placeholder="请选择">
            <el-option
              :label="channel.name"
              :value="channel.id"
              v-for='channel in channels'
              :key=channel.id
              ></el-option>
          </el-select> -->
          <ChannelSelect v-model='articleForm.channel_id'></ChannelSelect>
        </el-form-item>
        <el-form-item>
          <el-button type="primary" @click="onSubmit(false)">发表</el-button>
          <el-button @click="onSubmit(true)" >存入草稿</el-button>
        </el-form-item>
      </el-form>
    </el-card>
  </div>
</template>

<script>
// require styles
import 'quill/dist/quill.core.css'
import 'quill/dist/quill.snow.css'
import 'quill/dist/quill.bubble.css'
import { quillEditor } from 'vue-quill-editor'
import ChannelSelect from '@/components/channel-select'
import UploadImage from './components/upload-images'
export default {
  name: 'publishArticle',
  components: {
    // eslint-disable-next-line vue/no-unused-components
    quillEditor,
    ChannelSelect,
    'upload-image': UploadImage
  },
  data () {
    return {
      articleForm: {
        title: '',
        content: '',
        cover: {
          type: 1,
          images: []
        },
        channel_id: ''
      }
      // channels: []
    }
  },
  created () {
    this.loadArticles()
  },
  methods: {
    // 发布和编辑用的是同一事件 需要判断是编辑还是发布

    onSubmit (draft) {
      // 如果是编辑
      if (this.$route.params.articleID) {
        this.editArticle(draft)
      } else {
        this.publishArticle(draft)
      }

      // console.log('submit!')
    },
    editArticle (draft) {
      this.$axios({
        method: 'PUT',
        url: `/articles/${this.$route.params.articleID}`,
        params: {
          draft
        },
        data: this.articleForm

      }).then(res => {
        console.log(res)
        this.$router.push('/article')
        this.$message({
          message: '恭喜你，发布成功',
          type: 'success'
        })
      }).catch(err => {
        console.log(err)

        this.$message.error('发布失败')
      })
    },
    publishArticle (draft) {
      this.$axios({
        method: 'POST',
        url: '/articles',
        params: {
          draft
        },
        data: this.articleForm

      }).then(res => {
        console.log(res)
      })
    },

    loadArticles () {
      // console.log(this.$route.params.articleID)
      this.$axios({
        method: 'GET',
        url: `/articles/${this.$route.params.articleID}`

      }).then(res => {
        console.log(res)
        this.articleForm = res.data.data
      })
    }
    // loadChannels () {
    //   this.$axios({
    //     method: 'GET',
    //     url: '/channels'
    //   }).then(res => {
    //     // console.log(res)
    //     this.channels = res.data.data.channels
    //   })
    // }

  }

}
</script>

