<template>
  <div>
      <el-card class="box-card">
          <div slot="header" class="clearfix">
            <span>素材管理</span>
            <el-button
              style="float: right; padding: 3px 0"
              type="text"
            >上传图片</el-button>
          </div>
           <div>
              <el-radio-group
                v-model="type"
                style="margin-bottom: 20px"
                @change='onChange'
                >
                <el-radio-button label="全部"></el-radio-button>
                <el-radio-button label="收藏"></el-radio-button>
              </el-radio-group>
            </div>
          <el-row :gutter="20">
            <el-col :xs="24" :sm="12" :md="4" :lg="4"
              v-for="image in images"
              :key="image.id"
              style='margin-bottom:20px'
            >
               <el-card :body-style="{ padding: '0px' }">
                  <img
                    :src='image.url'
                    class="image"
                    height='200'
                  >
                  <div style="padding: 14px;">
                    <div class="bottom clearfix">
                      <!-- <i class="el-icon-star-off"></i> -->
                      <i class="{
                        `${image.is_collected} ? el-icon-star-on : el-icon-star-off`
                      }"></i>
                      <i class="el-icon-delete-solid"></i>
                    </div>
                  </div>
                </el-card>
            </el-col>
        </el-row>
      </el-card>
</div>
</template>

<script>
export default {
  name: 'Media-img',
  data () {
    return {
      images: [],
      type: '全部'
    }
  },
  created () {
    this.loadImages()
  },
  methods: {
    loadImages () {
      this.$axios({
        method: 'GET',
        url: '/user/images',
        params: {
          collect: false
        }

      }).then(res => {
        console.log(res)
        this.images = res.data.data.results
      })
    },
    onChange () {
      this.$axios({
        method: 'GET',
        url: '/user/images',
        params: {
          collect: this.type
        }
      }).then(res => {
        console.log(res)
      })
    }

  }

}
</script>

