<template>
  <div class="upload-image">
      <div class="upload-image">
        <div class="preview" @click="onUploadShow">
        <img :src="value" class="avatar" v-if="value">
        <i v-else class="el-icon-plus avatar-uploader-icon"></i>
        </div>
      </div>
      <el-dialog
        title="提示"
        :visible.sync="centerDialogVisible"
        width="50%"
        center>
        <el-tabs type="border-card" v-model="activeName">
            <el-tab-pane label="素材库" name="first">
                 <el-radio-group v-model="activeImage">
                    <el-radio-button label="全部"></el-radio-button>
                    <el-radio-button label="收藏"></el-radio-button>
                </el-radio-group>
                <el-row :gutter="20">
                    <el-col :span="6"
                     v-for="(item,index) in images"
                     :key="item.id"
                     :class="{
                      'activeBorder':index===activeIndex
                     }"
                     @click.native="activeIndex=index"
                    >
                        <div class="grid-content bg-purple">
                            <img :src="item.url" height="100">
                        </div>
                    </el-col>
                </el-row>
            </el-tab-pane>
            <el-tab-pane label="上传图片" name="second">
              <el-upload
                class="avatar-uploader"
                action="https://jsonplaceholder.typicode.com/posts/"
                :show-file-list="false"
                :headers="token"
                name="image"
                :on-preview="onPreview"
                >
                <i class="el-icon-plus avatar-uploader-icon"></i>
              </el-upload>
            </el-tab-pane>
        </el-tabs>
        <span slot="footer" class="dialog-footer">
            <el-button @click="centerDialogVisible = false">取 消</el-button>
            <el-button type="primary" @click="onConfirm">确 定</el-button>
        </span>
        </el-dialog>
  </div>
</template>
<script>
// 点击确定按钮的时候 把选中的图片的路径添加到image数组中
// 从而影响视图 这就又涉及到父子组件通信
const token = localStorage.getItem('token')
export default {
  data () {
    return {
      centerDialogVisible: false,
      activeName: 'first',
      activeImage: '全部',
      images: [],
      activeIndex: null,
      token: {
        Authorization: `Bearer ${token}`
      }
    }
  },
  props: {
    value: {
      type: String
    }
  },
  created () {
    //   在弹框弹出的时候再加载图片 而不是一开始就加载图片 ，所以加载图片的请求不在这
    // 这里写
  },
  methods: {
    onUploadShow () {
      this.uploadImages()
      this.centerDialogVisible = true
    },
    uploadImages () {
      this.$axios({
        method: 'GET',
        url: '/user/images',
        params: {
          collect: this.activeImage === 'collect'
        }
      }).then(res => {
        // console.log(res)
        this.images = res.data.data.results
      })
    },
    onConfirm () {
      // 点击确定的时候 把选择的图片的路径保存到数组中
      // 获取选中的图片 获取渲染到弹框的全部图片中的一张
      const image = this.images[this.activeIndex]
      // 如果有选中的图片 把选中的图片同步到父组件的数据中
      if (image) {
        this.$emit('input', image.url)
      }

      this.centerDialogVisible = false
    },
    onPreview (file) {
      console.log(file)
    }
  }
}
</script>
<style lang="less" scoped>
.upload-image {
  border: 1px dashed #d9d9d9;
  border-radius: 6px;
  cursor: pointer;
  position: relative;
  overflow: hidden;
}

.upload-image .el-upload:hover {
  border-color: #409EFF;
}

.avatar-uploader-icon {
  font-size: 28px;
  color: #8c939d;
  width: 178px;
  height: 178px;
  line-height: 178px;
  text-align: center;
}

.avatar {
  width: 178px;
  height: 178px;
  display: block;
}
.activeBorder {
border: 1px solid red
}

</style>
